body {
  font-family: Arial, sans-serif;
}

header {
  background-color: #f8f9fa;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav li {
  margin: 0 10px;
}

nav a {
  color: #007bff;
  text-decoration: none;
}

main {
  padding: 20px;
}

form {
  flex-direction: column;
  max-width: 400px;
  margin: auto;
  display: flex;
}

label {
  margin: 10px 0 5px;
}

input {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

button:hover {
  background-color: #0056b3;
}
